let regMob = /^1[34578]\d{9}$/
let regTel = /^((0\d{2,3})-)(\d{7,8})(-(\d{3,}))?$/

// 
const validateMobile = (rule, value, callback) => {
  if (value !== '' && !regMob.test(value)) {
    callback(new Error('!'))
  } else {
    callback()
  }
}
// 
const validateTel = (rule, value, callback) => {
  if (value !== '' && !regTel.test(value)) {
    callback(new Error('!'))
  } else {
    callback()
  }
}
// ---
const validatePhone = (rule, value, callback) => {
  if ((value !== '' && !regMob.test(value)) && (value !== '' && !regTel.test(value))) {
    callback(new Error('!'))
  } else {
    callback()
  }
}
// 
const validatePwd = (rule, value, callback) => {
  let reg = /^(?![0-9a-z]+$)(?![0-9A-Z]+$)(?![0-9\W]+$)(?![a-z\W]+$)(?![a-zA-Z]+$)(?![A-Z\W]+$)[a-zA-Z0-9\W_]+$/
  if (value === '') {
    callback(new Error(''))
  } else if (value.length < 8) {
    callback(new Error(''))
  } else if (!reg.test(value)) {
    callback(new Error(',,,,'))
  } else {
    callback()
  }
}
// 
const validateLength = (rule, value, callback) => {
  let length = rule['length']
  if (countLength(value) > length) {
    callback(new Error(`${length}`))
  } else {
    callback()
  }
}
function countLength (str) {
  let inputLength = 0
  for (let i in str) {
    let countCode = str.codePointAt(i)
    if (countCode >= 0 && countCode <= 128) {
      inputLength++
    } else {
      inputLength += 2
    }
  }
  return inputLength
}
// 
const validateNum = (rule, value, callback) => {
  let regu = /^[0-9]*$/
  if (value !== '' && !regu.test(value)) {
    callback(new Error(''))
  } else {
    callback()
  }
}
// 
const vaildateDecimal = (rule, value, callback) => {
  let decimal = rule['decimal']
  let reg = /^\d+\.?\d*$/
  if (pub.strim(value) !== '') {
    if (!reg.test(value)) {
      callback(new Error(''))
    } else {
      let arr = value.split('.')
      if (arr.length > 1) {
        if (arr[1].length > decimal) {
          callback(new Error(`${decimal}`))
        } else {
          callback()
        }
      } else {
        callback()
      }
    }
  } else {
    callback()
  }
}
// 
const requiredArr = (rule, value, callback) => {
  if (value.length <= 0) {
    callback(new Error(rule['message']))
  } else {
    callback()
  }
}
export {
  validateMobile,
  validateTel,
  validatePhone,
  validatePwd,
  validateLength,
  requiredArr,
  validateNum,
  vaildateDecimal
}

export default {
  validateMobile,
  validateTel,
  validatePhone,
  validatePwd,
  validateLength,
  requiredArr,
  validateNum,
  vaildateDecimal
}



